<template>
  <div class="tabActive">
    <dl>
      <dt>{{$t('other.active')}}</dt>
      <dd v-for="(list,key) in spanList" :key="key">
        <em>{{list.em}}</em>
        <span>{{list.span}}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
  export default {
    props:['spanList'],
    name: "",
    data() {
      return {
      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .tabActive{
    background-color: #eee;
    border-radius: .2rem;
    width: 100%;
    dl{
      padding: .36rem .8rem;
      dt{
        @include sc(.26rem, #000);
        padding-bottom: .26rem;
        text-align: center;
      }
      dd{
        @include sc(.2rem, #000);
        @include fjs;
        padding: .1rem 0;
        em{
          width: .8rem;
          font-style: normal;
          padding-right: .1rem;
        }
        span{
          width: 3.8rem;
        }
      }
    }
  }
</style>



